import { getFormStatus } from "@/helpers/FormsHelpers";
import { FormTypes } from "@/constants/FormConstants";

export const generalModule = {
  namespaced: true,
  state: {
    email: "",
    russianTechnology: "",
    englishTechnology: "",
    russianInformation: "",
    englishInformation: "",
    russianProgram: "",
    englishProgram: "",
  },
  getters: {
    getRussianInformation(state) {
      return state.russianInformation;
    },
    getEnglishInformation(state) {
      return state.englishInformation;
    },
    getEmail(state) {
      return state.email;
    },
    getRussianTechnology(state) {
      return state.russianTechnology;
    },
    getEnglishTechnology(state) {
      return state.englishTechnology;
    },
    getRussianProgram(state) {
      return state.russianProgram;
    },
    getEnglishProgram(state) {
      return state.englishProgram;
    },
    getFormStatus(state, _, rootState, rootGetters) {
      const formType = rootGetters["formInfo/getFormType"];

      const isTechType = formType === FormTypes.Tech;

      const hasEmail = !!state.email;
      const hasRussian = !!state.russianInformation;
      const hasEnglish = !!state.englishInformation;

      const hasRussianTechnology = isTechType
        ? !!state.russianTechnology
        : !!state.russianProgram;
      const hasEnglishTechnology = isTechType
        ? !!state.englishTechnology
        : !!state.englishProgram;

      const isFilled =
        hasEmail &&
        hasRussian &&
        hasEnglish &&
        hasEnglishTechnology &&
        hasRussianTechnology;
      const isNotFilled =
        !hasEmail &&
        !hasRussian &&
        !hasEnglish &&
        hasEnglishTechnology &&
        !hasRussianTechnology;

      return getFormStatus({ isFilled, isNotFilled });
    },
  },
  mutations: {
    changeRussianInformation(state, value) {
      state.russianInformation = value;
    },
    changeEnglishInformation(state, value) {
      state.englishInformation = value;
    },
    changeRussianProgram(state, value) {
      state.russianProgram = value;
    },
    changeEnglishProgram(state, value) {
      state.englishProgram = value;
    },
    changeEmail(state, value) {
      state.email = value;
    },
    changeRussianTechnology(state, value) {
      state.russianTechnology = value;
    },
    changeEnglishTechnology(state, value) {
      state.englishTechnology = value;
    },
    setForm(state, data) {
      state.email = data.email;
      state.russianTechnology = data.russianTechnology;
      state.englishTechnology = data.englishTechnology;
      state.russianInformation = data.russianInformation;
      state.englishInformation = data.englishInformation;
      state.russianProgram = data.russianProgram;
      state.englishProgram = data.englishProgram;
    },
  },
};
