import { sleep } from "@/utils/SystemUtils";
import { getFormStatus } from "@/helpers/FormsHelpers";

export const descriptionModule = {
  namespaced: true,
  state: {
    files: [],
    russianDescriptionProgram: "",
    englishDescriptionProgram: "",
    russianProgramLanguage: "",
    englishProgramLanguage: "",
    programSize: "",
    programSizeUnit: "kb",
    modifiedStatus: "original",
    russianRegisterNumber: "",
    englishRegisterNumber: "",
    usesPersonalData: true,
    russianPersonalData: "",
    englishPersonalData: "",
    russianPersonalDataTarget: "",
    englishPersonalDataTarget: "",
  },
  getters: {
    getFiles(state) {
      return state.files;
    },
    getRussianDescriptionProgram(state) {
      return state.russianDescriptionProgram;
    },
    getEnglishDescriptionProgram(state) {
      return state.englishDescriptionProgram;
    },
    getRussianProgramLanguage(state) {
      return state.russianProgramLanguage;
    },
    getEnglishProgramLanguage(state) {
      return state.englishProgramLanguage;
    },
    getRussianRegisterNumber(state) {
      return state.russianRegisterNumber;
    },
    getEnglishRegisterNumber(state) {
      return state.englishRegisterNumber;
    },
    getRussianPersonalData(state) {
      return state.russianPersonalData;
    },
    getEnglishPersonalData(state) {
      return state.englishPersonalData;
    },
    getRussianPersonalDataTarget(state) {
      return state.russianPersonalDataTarget;
    },
    getEnglishPersonalDataTarget(state) {
      return state.englishPersonalDataTarget;
    },
    getProgramSize(state) {
      return state.programSize;
    },
    getProgramSizeUnit(state) {
      return state.programSizeUnit;
    },
    getModifiedStatus(state) {
      return state.modifiedStatus;
    },
    getUsesPersonalData(state) {
      return state.usesPersonalData;
    },
    getFormStatus(state) {
      const hasFiles = state.files.length > 0;

      const isFilled = hasFiles;
      const isNotFilled = !hasFiles;

      return getFormStatus({ isFilled, isNotFilled });
    },
  },
  mutations: {
    changeRussianDescriptionProgram(state, value) {
      state.russianDescriptionProgram = value;
    },
    changeEnglishDescriptionProgram(state, value) {
      state.englishDescriptionProgram = value;
    },
    changeRussianProgramLanguage(state, value) {
      state.russianProgramLanguage = value;
    },
    changeEnglishProgramLanguage(state, value) {
      state.englishProgramLanguage = value;
    },
    changeRussianRegisterNumber(state, value) {
      state.russianRegisterNumber = value;
    },
    changeEnglishRegisterNumber(state, value) {
      state.englishRegisterNumber = value;
    },
    changeRussianPersonalData(state, value) {
      state.russianPersonalData = value;
    },
    changeEnglishPersonalData(state, value) {
      state.englishPersonalData = value;
    },
    changeRussianPersonalDataTarget(state, value) {
      state.russianPersonalDataTarget = value;
    },
    changeEnglishPersonalDataTarget(state, value) {
      state.englishPersonalDataTarget = value;
    },
    changeProgramSize(state, value) {
      state.programSize = value;
    },
    changeProgramSizeUnit(state, value) {
      state.programSizeUnit = value;
    },
    changeModifiedStatus(state, value) {
      state.modifiedStatus = value;
    },
    changeUsesPersonalData(state, value) {
      state.usesPersonalData = value;
    },
    addFiles(state, files) {
      const list = state.files.slice();

      state.files = list.concat(files);
    },
    removeFile(state, index) {
      const list = state.files.slice();

      list.splice(index, 1);

      state.files = list;
    },
    setForm(state, data) {
      state.files = data.files;
    },
  },
  actions: {
    async uploadFiles(store, files) {
      const restFiles = Object.values(files)
        .filter((x) => x.size < 104857600)
        .map((x) => ({ name: x.name }));

      try {
        // Имитация запроса на сервер
        await sleep(2000);

        store.commit("addFiles", restFiles);
      } catch (e) {
        return e;
      }
    },
  },
};
